import { Component } from '@angular/core';
import { NavController, NavParams, IonicPage } from 'ionic-angular';
import { InAppBrowser, InAppBrowserOptions } from '@ionic-native/in-app-browser';

/**
 * Generated class for the WebViewPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-web-view',
  templateUrl: 'web-view.html',
})
export class WebViewPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public iab: InAppBrowser) {
    
  }

  ionViewWillEnter(){
    // const options: InAppBrowserOptions = {
    //   zoom: 'no',
    //   location: 'no',
    // }

    // const browser = this.iab.create('http://cponpaint.com.vn/', '_target', options);
  }

  openBrowser() {
    const options: InAppBrowserOptions = {
      zoom: 'no',
      location: 'no',
    }

    const browser = this.iab.create('http://cponpaint.com.vn/', '_target', options);
  }

}



